// clearfix
@mixin clearfix($extend: true) {
  @if ($extend) {
    &::before,
    &::after {
      content: "";
      display: table;
    }

    &::after {
      clear: both;
    }
  }
}
// Hide only visually, but have it available for screenreaders
// 只隐藏于视觉，屏幕浏览器可以阅读
@mixin hidden-clip($extend: true) {
  @if ($extend) {
    position: absolute;
    clip: rect(1px, 1px, 1px, 1px);
  }
}

// ellipsis
@mixin ellipsis($extend: true) {
  @if ($extend) {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
}

// 只有webkit支持多行省略号
@mixin ellipsis-lines($lines: 2) {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: $lines;
  -webkit-box-orient: vertical;
}

// word-break
// 用于链接地址强制换行
@mixin word-break($extend: true) {
  @if ($extend) {
    white-space: normal;
    word-wrap: break-word;
    word-break: break-all;
  }
}

// text-center
// 文本位置
@mixin text-align($textPos: center) {
  text-align: $textPos;
}
// font-size
@mixin font-size($px, $f: 0, $impt: null) {
  @if ($f==0) {
    font-size: px2rem($px) $impt;
  } @else {
    font-size: ($px/2) $impt;
  }
}
@mixin bg-color($c, $impt: null) {
  background-color: $c $impt;
}

$designWidth: 750;
$maxWidth: 1080px;

@function px2rem($px) {
  @return $px * 320 / $designWidth/20px + rem;
}
$color_fff: #fff;
$color_black: #272a2a;
$color_282c34: #282c34;

html,
body {
  width: 100%;
  -webkit-overflow-scrolling: touch;
  line-height: 1.4;
  @include font-size(30px);
  -webkit-text-size-adjust: 100% !important;
  text-size-adjust: 100% !important;
}

body {
  font-family: "PingFangSC-Regular", "Microsoft Yahei", Helvetica, Arial, sans-serif;
  font-weight: 400;
  color: $color_black;
  @include bg-color($color_fff);
  // -webkit-user-select: none;
  -webkit-tap-highlight-color: transparent;
  outline: 0;
  position: relative;
  top: 0;
  right: 0;
  left: 0;
}

.nav {
  background-color: $color_282c34;
  &_main {
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: px2rem(10px) px2rem(20px);
    max-width: $maxWidth;
  }
}
.link {
  font-size: px2rem(30px);
  color: #fff;
  text-decoration: auto;
}
.main {
  margin: auto;
  max-width: $maxWidth;
}
